<template>
  
  <div class="home">
  
    <!--  -->
    <nav-bar>
      <!-- <template v-slot:left>&lt;</template> -->
      <template v-slot:default></template>
      <template v-slot:right></template>
    </nav-bar>


   <!--   -->
      <tab-control class="topTabFixed"
                   v-show="isTabFixed"
                   @tabClick="tabClick" 
                   :titles="['','','']">
      </tab-control>
      

  <div class="wrapper" >

    <div class="content">
  
        <div ref="banref">
            <!-- banner -->
            <home-swiper :banners="banners"></home-swiper>
            <!--  -->
            <recommend-view :recommends="recommends"></recommend-view>
        </div>
 
 

      <!--  -->
      <tab-control @tabClick="tabClick" :titles="['','','']">
      </tab-control>


      <!-- -->
      <goods-list :goods="showGoods"></goods-list>


    </div>

  </div>

 

  </div>

</template>


<script>

import HomeSwiper from "./ChildComps/HomeSwiper.vue";
import NavBar from 'components/common/navbar/NavBar';
import RecommendView from "./ChildComps/RecommendView";
import tabControl from 'components/content/tabControl/tabControl'
import GoodsList from 'components/content/goods/GoodsList'
import { getHomeAllData, getHomeGoods} from "network/home";



import {ref, reactive, onMounted, computed, watchEffect, nextTick } from "vue";
import BScroll from 'better-scroll';


export default {
  name: 'Home',
  setup() {

      let isTabFixed = ref(false);
      let banref = ref(null);    
      //
      let temid = ref(0);
      const recommends = ref([]);
       let banners = ref([]);



      //
     const goods = reactive({
       sales: {page: 1, list:[]},
       new: {page: 1, list:[]},
       recommend: {page: 1, list:[]},
     })
     
     //
    let currentType = ref("sales");
    const showGoods = computed( ()=>{
      return goods[currentType.value].list;
    })


    let bscroll = reactive({})
    onMounted(() => {

      getHomeAllData().then(res=>{
         recommends.value = res.goods.data;
         banners.value = res.slides;
      })

      getHomeGoods("sales").then(res=>{
         goods.sales.list = res.goods.data;
      })
       getHomeGoods("recommend").then(res=>{
         goods.recommend.list = res.goods.data;
      })
       getHomeGoods("new").then(res=>{
          goods.new.list = res.goods.data;
      })

      //BetterScroll
       bscroll = new BScroll(document.querySelector('.wrapper'), {
        probeType: 3,
        click: true,
        pullUpLoad: true //false
      });

      //
      bscroll.on("scroll", (position)=>{
         isTabFixed.value = (-position.y) > banref.value.offsetHeight;
      })

      //
      bscroll.on("pullingUp",()=>{

        // console.log("");
        // console.log(document.querySelector(".content").clientHeight);

        //
        const page = goods[currentType.value].page + 1;
        getHomeGoods(currentType.value, page).then(ref=>{
           goods[currentType.value].list.push(...ref.goods.data)
           goods[currentType.value].page  += 1
        })
        console.log("" + currentType.value + "" + page);

        //
        bscroll.finishPullUp();

        //
        bscroll.refresh();
        
      })

    })


    //
      watchEffect(()=>{
        nextTick(()=>{
          //DOM
            bscroll && bscroll.refresh();
        })
      })





    const tabClick = (index) => {
        let types = ["sales","recommend","new"];
        currentType.value = types[index];

        nextTick(()=>{
          //DOM
            bscroll && bscroll.refresh();
        })

    }

    return {
      recommends,
      tabClick,
      goods,
      showGoods,
      isTabFixed,
      banref,
      banners
    }

  },

  components: {
    HomeSwiper,
    NavBar,
    RecommendView,
    tabControl,
    GoodsList
  }

}

</script>


<style scoped >

.banners img{
  width: 100%;
  height: auto;
}


#home {
  height: 100vh;
  position: relative;
}

.topTabFixed {
   margin-top: 45px;
}

.wrapper {
  position: absolute;
  top: 45px;
  bottom: 50px;
  left: 0px;
  right: 0;
  overflow: hidden;
}



</style>


